<template lang="html">
    <div class="demo">

        <p class="demo-list" v-on:click="showPickerAddress1">['', '', ''] <span>{{resAddress1}}</span></p>

        <p class="demo-list" v-on:click="showPickerAddress2">['', '', ''] <span>{{resAddress2}}</span></p>

        <p class="demo-list" v-on:click="showPickerAddress3">['', ''] <span>{{resAddress3}}</span></p>

        <!--  -->
        <pickerAddress
            v-bind:Metadata="Metadata"
            v-on:accept-result="acceptResultAddress1"
            ref="pickerAddress1"
        ></pickerAddress>

        <!--  -->
        <pickerAddress
            v-bind:Metadata="Metadata"
            v-bind:setAddress="['', '', '']"
            v-on:accept-result="acceptResultAddress2"
            ref="pickerAddress2"
        ></pickerAddress>

        <!--  -->
        <pickerAddress
            v-bind:Metadata="Metadata"
            v-bind:setProcity="true"
            v-on:accept-result="acceptResultAddress3"
            ref="pickerAddress3"
        ></pickerAddress>




    </div>
</template>

<script>

import metadata from '../../assets/AreaData.json';

export default {
    name:'picker',
    data(){
        return{
            msg:'picker',
            Metadata:metadata,
            address:['', '', ''],
            date:[2015,5,5],
            resAddress1:'//',
            resAddress2:'//',
            resAddress3:'/',
        }
    },
    components:{
    },
    methods:{
        // 
        showPickerAddress1(){
            this.$refs.pickerAddress1.open();
        },
        acceptResultAddress1(val){
            this.resAddress1 = val.toString();
        },

        // 
        showPickerAddress2(){
            this.$refs.pickerAddress2.open();
        },
        acceptResultAddress2(val){
            this.resAddress2 = val.toString();
        },

        // 
        showPickerAddress3(){
            this.$refs.pickerAddress3.open();
        },
        acceptResultAddress3(val){
            this.resAddress3 = val.toString();
        },


    }
}
</script>



